.mobile-menu {
  width: 100vw;
  height: 100vh;
  z-index: 99;
  background-color: #e17992;
  flex-direction: column;
  padding-top: 20px;
  transition: transform .5s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  transform: translateX(100%);
}

.mobile-menu__buy-btn {
  width: 209px;
  height: 44px;
  color: #d41443;
  background-color: #fff;
  border: none;
  border-radius: 22px;
  justify-content: space-evenly;
  align-self: center;
  align-items: center;
